@import '#styles/variables';

.wrapper{

}

.skillsContainer{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 4rem;
}
.title{
  font-size: 2rem;
  text-align: center;
  margin: 4rem 0;
}
.svg{
  position: absolute;
  top: 50%;
  left: 50%;
  width: 85%;
  height: 85%;
  transform: translateX(-50%) translateY(-50%);
}


@media(min-width: $L){
  .skillsContainer{
    grid-template-columns: repeat(3, 1fr);
  }
}
@media(min-width: $X){
  .skillsContainer{
    grid-template-columns: repeat(4, 1fr);
  }
}