.wrapper{
  position: relative;
  display: inline-block;
  padding: 1rem 0;
  min-width: 7rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  font-size: 1.5rem;
  box-sizing: content-box;
  font-weight: bold;
  transition: .5s;
  overflow: hidden;
  color: #03e9f4;
  background-color: transparent;
    & span{
    position: absolute;
    display: block;
  }
  & span:nth-child(1){
    top:0;
    left:0;
    width: 100%;
    height: 1px;
    background: linear-gradient(90deg, transparent, #03e9f4);
    animation: animate1 4s infinite;
  }
  & span:nth-child(2){
    top:-100%;
    right:0;
    width: 1px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #03e9f4);
    animation: animate2 4s infinite;
    animation-delay: 1s;

  }
  & span:nth-child(3){
    bottom:0;
    right:0;
    width: 100%;
    height: 1px;
    background: linear-gradient(270deg, transparent, #03e9f4);
    animation: animate3 4s infinite;
    animation-delay: 2s;
  }
  & span:nth-child(4){
    bottom:-100%;
    left:0;
    width: 1px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #03e9f4);
    animation: animate4 4s infinite;
    animation-delay: 3s;
 
  }
}
  .active{
    background-color: #03e9f4;
    // color: transparent;
    color: #3f3f3f;
    box-shadow: 0 0 5px #03e9f4,
                0 0 25px #03e9f4,
                0 0 50px #03e9f4,
                0 0 200px #03e9f4;
  }



  @keyframes animate1 {
    0%{
      left: -100%;
    }
    100%{
      left: 100%;
    }
  }
  @keyframes animate2 {
    0%{
      top: -100%;
    }
    100%{
      top: 100%;
    }
  }
  @keyframes animate3 {
    0%{
      right: -100%;
    }
    100%{
      right: 100%;
    }
  }
  @keyframes animate4 {
    0%{
      bottom: -100%;
    }
    100%{
      bottom: 100%;
    }
  }


