html{
  font-size: 80%;
  overflow-x: hidden;
}
body{
  font-family: 'Tektur', cursive;
}
#root{
  height: 100%;
}

.App{
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
  overflow-x: hidden;
}
header{
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  height: $heightHeader;
  max-width: $maxWidth;
  transform: translateX(-50%);
  z-index: 3;
}
main{
  position: relative;
  margin-top: $heightHeader;
  width: 100%;
  flex-grow: 1;
  max-width: $maxWidth;
  justify-content: center;
  height: inherit;
  z-index: 2;
}
.background{
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  transform: translateX(-50%);
  z-index: 1;
}


footer{
  width: 100%;
  max-width: $maxWidth;
  height: $heightFooter;
  bottom: 0;
  left: 0;
  z-index: 3;
}




@media(min-width:$XXL){
  .background{
    width: 100%;
  }
}

@media(max-width:$L){
  html{
    font-size: 70%;
  }
}
@media(max-width:$S){
  html{
    font-size: 65%;
  }
}