.aaa-enter {
  top: -10%;
}
.aaa-enter-active {
  top: 0;
  transition: top 1500ms;
}
.aaa-exit {
  top: 0;
}
.aaa-exit-active {
  top: -10%;
  transition: top 1500ms;
}



.bbbbb-enter {
  transform: translateX(-100%);
}
.bbbbb-enter-active {
  transform: translateX(0%);
  transition: transform 750ms;
}
.bbbbb-exit {
  transform: translateX(0%);
  transition: transform 750ms;
}
.bbbbb-exit-active {
  transform: translateX(100%);
}
